body{
  background: black;
  
}
.card{
  width:20rem;
}

.card-hover{
  box-shadow: 5px 10px 20px 1px ; color: black;
  transform:scale(1.3)
}

.card-body{
  padding: 3rem 0 !important;
}

.card-text{
  font-size:0.9rem;
  padding:0.4rem 1.9rem;
}

.container-fluid.row {
  padding-top :6rem;
}

.overflow{
  overflow-y: hidden;
  overflow: hidden;
}

.card-img-top{
  transform:scale(1);
  transition: transform 0.5s ease;
  overflow:hidden;
}

.card-img-top:hover{
  transform:scale(1.3);
  overflow:hidden
}